* {
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell',
    'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
}

nav {
  padding: 0 1rem;
}

.content {
  min-height: calc(100vh - 70px - 64px);
  padding: 1.5rem 0;
}

.goods {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1rem;
}

.card__price {
  font-size: 1.4rem;
}

.card {
  display: flex;
  flex-direction: column;
}

.card .card-content {
  flex-grow: 1;
}

.card .card-action {
  display: flex;
  justify-content: space-between;
}

.cart {
  position: fixed;
  top: 5rem;
  right: 2rem;
  cursor: pointer;
  z-index: 5;
  padding: 1rem;
}

@media (max-width: 767px) {
  .cart {
    bottom: 2rem;
    top: unset;
  }
}

.basket-list {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #fff;
  width: 50rem;
  max-width: 95%;
  box-shadow: inset -1px 3px 8px 5px #1f87ff, 2px 5px 16px 0px #0b325e,
    5px 5px 15px 5px rgba(0, 0, 0, 0);
  animation: show 500ms ease-in-out;
}

.basket-close {
  cursor: pointer;
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
  color: #fff;
}

.basket-delete {
  cursor: pointer;
}

.basket-quantity {
  cursor: pointer;
  vertical-align: middle;
  color: #26a69a;
  margin: 0 5px;
}

.basket-quantity:first-of-type {
  margin-right: 0;
}

.collection .collection-item {
  user-select: none;
}

#toast-container {
  top: 2.8rem !important;
  animation: fade-in 250ms ease-in-out;
}

@media (max-width: 600px) {
  #toast-container {
    top: 2rem !important;
  }
  .toast {
    width: 90%;
  }
  .collection-item__span {
    display: block;
    margin-bottom: 5px;
  }
  .basket-quantity:first-of-type {
    margin-left: 0;
  }
}

@keyframes show {
  from {
    top: 70%;
    opacity: 0;
  }
  to {
    top: 50%;
    opacity: 1;
  }
}

@keyframes fade-in {
  from {
    opacity: 0;
    transform: translateY(2rem);
  }
  to {
    opacity: 1;
    transform: translateY(0rem);
  }
}
